.nav {
	display: flex;
	justify-content: space-between;
	width: 100%;
	padding: 20px;
	height: 30px;
	position: fixed;
	z-index: 1;
	top:0;
	transition-timing-function: ease-in;
	transition: all 0.5s;
}
.nav_black {
	background-color: #111;
}
.nav_logo {
	position: fixed;
	left: 20px;
	width: 80px;
	object-fit: contain;
}
.nav_avatar {
	position: fixed;;
	right: 20px;
	width:30px;
	object-fit: contain;
}